import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { genericColumn } from "../../genericColumn"

function period(_tableName: string) {
  const title = "Period"
  return genericColumn({
    endpoint: "companyBalanceStatement",
    columnKeys: ["period"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "The financial period type.",
    cell: info => {
      return (
        <WatchlistDataTableTextCell
          value={info.row.original.financialData.companyBalanceStatement?.data?.period}
          queryResult={info.row.original.financialData.companyBalanceStatement}
        >
          {info.row.original.financialData.companyBalanceStatement?.data?.period}
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { period }
